* {
  margin: 0;
  padding: 0;
}

.search-bar {
  height: 50px;
  background-color: pink;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding-top: 15px;
  display: flex;
}

.search-form input {
  width: 60%;
  max-width: 400px;
  color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  box-shadow: 0 0 6px #000;
}

#search-button {
  color: #fff;
  cursor: pointer;
  background-color: #138065;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 18px;
  box-shadow: 0 0 6px #000;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 30%;
  height: 100%;
  border-radius: 5px;
  margin-bottom: 60px;
  overflow: hidden;
  box-shadow: 0 0 6px #000;
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity .2s ease-in-out;
}

.photo-card:hover img {
  opacity: .8;
}

.load-more {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #138065;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 18px;
  display: none;
}

@media screen and (max-width: 768px) {
  .gallery {
    padding: 20px;
  }

  .photo-card {
    width: 45%;
  }
}

@media screen and (max-width: 480px) {
  .photo-card {
    width: 100%;
  }
}

